<template>
	<div style="z-index: 999999999999999999;" class="container-cust">
		<div id="timer-inline">
			<span id="minutes" :class="[totalTime <= 700 ? 'blink-red' : '']">{{ minutes }}</span>
			<!-- <span v-if="totalTime >= 600" id="middle" :class="[totalTime <= 700 ? 'blink-red' : '']">:</span> -->
			<!-- <span
				v-if="totalTime >= 600"
				id="seconds"
				:class="[totalTime <= 700 ? 'blink-red' : '']"
			>{{ seconds }}</span>-->
		</div>
	</div>
</template>

<script>
	import Event from "@/utils/Event.js";
	import axios from "axios";

	export default {
		data: () => ({
			timer: null,
			totalTime: 960,
			resetButton: false
		}),
		methods: {
			startTimer() {
				this.timer = setInterval(() => this.countdown(), 10);
				this.resetButton = true;
			},
			storeTimer() {
				this.timer = setInterval(() => this.storetime(), 10000);
				this.resetButton = true;
			},
			stopTimer() {
				this.timer = null;
				this.resetButton = true;
			},
			resetTimer() {
				this.totalTime = 960;
				clearInterval(this.timer);
				this.timer = null;
				this.resetButton = false;
			},
			padTime(time) {
				return (time < 10 ? "0" : "") + time;
			},
			countdown() {
				if (this.totalTime >= 1) {
					this.totalTime--;
				} else {
					localStorage.setItem("sI7m2xsXhH-z", 1);
					Event.$emit("timer-finished");
				}
			},
			async storetime() {
				const user = JSON.parse(localStorage.getItem("sI7m2xsXhH-z-u"));
			}
		},
		computed: {
			minutes() {
				const minutes = Math.floor(this.totalTime / 60);
				return this.padTime(minutes);
			},
			seconds() {
				const seconds = this.totalTime - this.minutes * 60;
				return this.padTime(seconds);
			}
		},
		mounted() {
			Event.$on("start-timer", () => {
				this.startTimer();
			});
			Event.$on("stop-timer", () => {
				localStorage.setItem("tt", this.totalTime);
				this.resetTimer();
			});
		}
	};
</script>

