<template>
	<div class="home">
		<Home/>
		<Timer id="timer"></Timer>
	</div>
</template>

<script>
	// @ is an alias to /src
	import Home from "@/components/Home.vue";
	import Timer from "@/components/Timer.vue";
	import Event from "@/utils/Event.js";

	export default {
		name: "home",
		components: {
			Home,
			Timer
		},
		mounted() {
			const timer = localStorage.getItem("sI7m2xsXhH-z");
			if (timer === 1) return Event.$emit("timer-finished");
			Event.$emit("start-timer");
		}
	};
</script>

<style>
	#logo {
		height: 5rem;
	}
</style>
