<template>
	<div class="home">
		<div v-show="isModal" id="myModal" class="modal-c">
			<!-- Modal content -->
			<div class="modal-content">
				<span style="text-align: right;" @click="close" class="close">&times;</span>
				<span style="text-align: center; font-family: Circular-Bold; color: darkgreen;">CLAIM YOUR PRIZE</span>
				<br>
				<input id="phone" type="text" placeholder="Enter 080XXXXXXXX" v-model="phoneNumber">
				<button @click.prevent="doSubmit" id="submit">SUBMIT</button>
				<span
					style="text-align: center; margin: 1rem auto; font-family: Circular-Bold; color: red; font-size: 0.8rem"
				>*** Make sure your phone number is valid. ***</span>
			</div>
		</div>
		<div id="caption">
			<img src="/src/caption.png" alt>
		</div>
		<h1 class="caption">Re arrange the boxes to WIN.</h1>
		<div class="container">
			<div id="game">
				<transition-group
					id="grid-answers"
					tag="div"
					name="sortableCards"
					class="grid single fourths flex-start-content disable-marker cards clickable sortable-animation"
					swappable
				>
					<div
						v-for="(image, index) in images"
						:key="index + 1"
						swapItem
						class="images"
						style="border-radius: 10px;"
						:data-target-generators="index"
					>
						<img class="game-cust" :data-target-generators="index" :src="image" alt>
					</div>
				</transition-group>
			</div>
		</div>
	</div>
</template>

<script>
	import { Swappable, Sortable } from "@shopify/draggable";
	import Event from "@/utils/Event.js";
	import axios from "axios";
	import izitoast from "izitoast";

	export default {
		data() {
			return {
				phoneNumber: "",
				images: ["src/2.png", "src/4.png", "src/3.png", "src/1.png"],
				isModal: false,
				answer: "3021",
				saved: false
			};
		},
		methods: {
			close() {
				if (this.saved === true) {
					this.isModal = false;
					return (window.location.href = "/unveil/finish");
				}
				this.isModal = false;
				return (window.location.href = "/unveil/finish");
			},
			async doSubmit() {
				const body = {
					phone: this.phoneNumber,
					time: localStorage.getItem("tt")
				};
				try {
					const { data, error } = await axios.post(
						"https://stay-sharp.herokuapp.com/topmint",
						body
					);
					if (data === "SAVED") {
						this.saved = true;
						izitoast.show({
							message:
								"Congratulations, your prize will be sent to you in less than 48hrs",
							messageColor: "white",
							backgroundColor: "green",
							onClosing: () => {
								return (window.location.href = "/unveil/finish");
							},
							position: "topRight",
							timeout: 3000
						});
					}
				} catch (error) {
					let message = "Error encountered while saving.";
					if (error.response.status === 419)
						message = "You have already played the game.";
					if (error.response.status === 400) message = "Phone number is invalid!";
					if (error.response.status === 403)
						message = "This service is valid for Nigerian residents only!";
					izitoast.show({
						message,
						messageColor: "white",
						backgroundColor: "red",
						position: "topRight",
						timeout: 3000,
						onClosing: () => {
							return (window.location.href = "/unveil/finish");
						}
					});
				}
			}
		},
		mounted() {
			const swappable = new Sortable(document.querySelectorAll("[swappable]"), {
				draggable: "[swapItem]",
				delay: 1
			});
			swappable.on("drag:stop", evt => {
				const parent = evt.data.sourceContainer.children;
				let values = "";
				for (let i = 0; i < parent.length; i++) {
					const data = parent[i].dataset.targetGenerators;
					if (i <= 3) {
						values += data;
					}
				}
				if (values === this.answer) {
					this.isModal = true;
					return Event.$emit("stop-timer");
				}
			});
		}
	};
</script>

